.workout-card {
  width: 100vw;
  max-width: 600px;
  min-width: 400px;
  min-height: 50px;
  position: relative;
  border-radius: 2px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin: 5px;
  width: 100%;
  height: 100%;
  background-color: #fff;
  box-sizing: border-box;
}

.workout-card__cat {
  height: 50px;
  width: 100px;
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
  background-color: #333;
  color: #fff;
}
.workout-card__block {
  height: 100%;
  width: 100px;
  margin: auto 5px;
}