.card {
  min-height: 80px;
  width: 150px;
  background-color: #fff;
  color: #111;
  position: relative;
  text-align: center;
  margin: 5px 5px;
  cursor: pointer;
  border-radius: 2px;
}
.card:hover {
  background-color: #ddd;
}

.card__bottom {
  width: 100%;
  height: 30px;
  text-align: center;
  font-size: 1.1em;
  background-color: #333;
  color: #fff;
  position: absolute;
  left: 0;
  bottom: 0;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
}

.card__title {
  font-size: 1.2em;
  color: #333;
}